<script>
import textView from './v.s.text.vue'
export default {
    name:'TEXT-STANDARD',
   extends:textView
}
</script>
<style lang="less" >
.TEXT-STANDARD
{
        display:inline-grid;
        grid-template-columns:1fr;
        grid-auto-rows:max-content;
        width:100%;
        height:100%;
        min-width: 0px;
        min-height: 0px;
        background:black;
        color:white;
        box-sizing:border-box;
        font-size: var(--logo-length,0.8em);
        box-sizing:border-box;
        justify-content:center;
        align-content:center;
        text-align: center;
}
</style>


